.header {
  width: 100%;
  border: 1px solid red;
  border: 1px solid #EAEAEA;
}

.header-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 127px;
}

.header__signin-img {
  cursor: pointer;
}

.header__left-block {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.header__logo-block-img {
  margin-right: 16px;
}

.header__logo-img {
  width: 40px;
  height: 40px;
}

.header__logo-title {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  color: #000000;
}

@media screen and (max-width: 823px) {
  .header__logo-title {
    display: none;
  }
}

.header__logo-description {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #9D9D9D;
}

@media screen and (max-width: 823px) {
  .header__logo-description {
    display: none;
  }
}

.header__list-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.header__item-info:not(:first-child) {
  margin-left: 30px;
}

.header__item-info--cart {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.header__price {
  margin-left: 10px;
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #5C5C5C;
}
