.baner{
    width: 100%;
    margin-top: 45px;
    margin-bottom: 39px;
    &-container{
        border-radius: 20px;
        background-repeat: no-repeat;
        min-height: 300px;
        padding: 80px 0 0 60px;
        background-size: cover;
    }
    &-content{
       
        
        max-width: 241px;
    }
    &__title{
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 38px;
        line-height: 46px;
        padding: 0px;
        margin-bottom: 10px;
        
        &-name{
            color: #8BB43C;
        }
    }
    &__button{
        display: inline-flex;
        justify-content: center;
        align-items: center;
        color:white;
        background-color: #8BB43C;
        padding:14px 54px;
        border-radius: 110px;
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        text-transform: uppercase;
        cursor: pointer;
        color: #FFFFFF;
        &:hover{
            background: white;
            color:#8BB43C;
            border:1px solid #8BB43C;
        }
    }
}