.product{
    flex:0 0 210px;
    padding:0px 33px;
    border-radius:40px;
    border: 1px solid #F3F3F3;
    transition: all 300ms ease;
   // background-color:rgba(235, 228, 228,.4);
    @media screen and (max-width:959px){
        flex:0 1 350px;
       
        
    }
    @media screen and (max-width:823px){
        flex:0 0 100%;
      
        
    }
    &:hover{
        transform: translateY(-10px);
        box-shadow: 0 0 10px #868484;;
    }
    &__body{
        position: relative;
        margin-bottom: 14px;
    }
    &__icon-like{
        display: block;
        position: absolute;
        top:29px;
        cursor: pointer;
       
    }
    &__img{
        width: 133px;
        height: 112px;
        display: block;
        @media screen and (max-width:959px){
            margin: auto;
            
        }
        margin-top:20px;
    }
    &__description{
        font-family: Inter;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;

        color: #000000;
        margin-bottom: 14px;
        @media screen and (max-width:959px){
          text-align: center;
            
        }
    }

    &__footer{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 14px;
        &-title{
            font-family: Inter;
            font-style: normal;
            font-weight: 500;
            font-size: 11px;
            line-height: 13px;
            text-transform: uppercase;
            
            color: #BDBDBD;
            display: inline-block;
            
        }
        &-price{
            font-family: Inter;
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 17px;
            /* identical to box height */
            
            
            color: #000000;
        }
        &-img{
            cursor: pointer;
        }
    }

}