.list-product {
  width: 100%;
}

.list-product-container {
  padding: 0 60px;
  margin-bottom: 40px;
}

@media screen and (max-width: 455px) {
  .list-product-container {
    padding: 0 10px;
  }
}

.list-product .list-product-top {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 30px;
}

@media screen and (max-width: 455px) {
  .list-product__title {
    display: none;
  }
}

.list-product__input-block {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 250px;
          flex: 0 0 250px;
  position: relative;
}

.list-product__btn-search {
  position: absolute;
  top: 13px;
  right: 10px;
  cursor: pointer;
}

.list-product__input {
  height: 45px;
  display: block;
  width: 100%;
  outline: none;
  border: 1px solid #F3F3F3;
  border-radius: 10px;
}

.list-product-galerie {
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  row-gap: 40px;
}

@media screen and (max-width: 959px) {
  .list-product-galerie {
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
  }
}

::-webkit-input-placeholder {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #C4C4C4;
  padding: 20px;
}

:-ms-input-placeholder {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #C4C4C4;
  padding: 20px;
}

::-ms-input-placeholder {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #C4C4C4;
  padding: 20px;
}

::placeholder {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #C4C4C4;
  padding: 20px;
}
