.product {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 210px;
          flex: 0 0 210px;
  padding: 0px 33px;
  border-radius: 40px;
  border: 1px solid #F3F3F3;
  -webkit-transition: all 300ms ease;
  transition: all 300ms ease;
}

@media screen and (max-width: 959px) {
  .product {
    -webkit-box-flex: 0;
        -ms-flex: 0 1 350px;
            flex: 0 1 350px;
  }
}

@media screen and (max-width: 823px) {
  .product {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
            flex: 0 0 100%;
  }
}

.product:hover {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  -webkit-box-shadow: 0 0 10px #868484;
          box-shadow: 0 0 10px #868484;
}

.product__body {
  position: relative;
  margin-bottom: 14px;
}

.product__icon-like {
  display: block;
  position: absolute;
  top: 29px;
  cursor: pointer;
}

.product__img {
  width: 133px;
  height: 112px;
  display: block;
  margin-top: 20px;
}

@media screen and (max-width: 959px) {
  .product__img {
    margin: auto;
  }
}

.product__description {
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  color: #000000;
  margin-bottom: 14px;
}

@media screen and (max-width: 959px) {
  .product__description {
    text-align: center;
  }
}

.product__footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 14px;
}

.product__footer-title {
  font-family: Inter;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 13px;
  text-transform: uppercase;
  color: #BDBDBD;
  display: inline-block;
}

.product__footer-price {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #000000;
}

.product__footer-img {
  cursor: pointer;
}
