.list-product{
    width:100%;
    &-container{
        
        padding:0 60px;
        margin-bottom: 40px;
        @media screen and (max-width:455px){
            padding:0 10px;
            
        }
        
        
    }
    .list-product-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 30px;
    }
    &__title{
        @media screen and (max-width:455px){
            display: none;
            
        }
    }
    &__input-block{
        flex:0 0 250px;
        position: relative;
    }
    &__btn-search{
        position: absolute;
        top:13px;
        right:10px;
        cursor: pointer;
    }
    &__input{
        height: 45px;
        display: block;
        width: 100%;
        outline: none;
        border: 1px solid #F3F3F3;
        border-radius: 10px;
    }


    &-galerie{
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        row-gap: 40px;
        @media screen and (max-width:959px){
            
            align-items: center;
        }
        
       
        
    }
}

::placeholder{
    font-family: Inter;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    /* identical to box height */
    
    
    color: #C4C4C4;
    padding:20px;
}