.header{
   
    width: 100%;
    border:1px solid red;
    border: 1px solid #EAEAEA;
   
    &-container{
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-height: 127px;
       
    }
    &__signin-img{
        cursor: pointer;
    }

    &__left-block{
        display: flex;
        align-items: center;
    }
    &__logo-block-img{
        margin-right: 16px;
    }
    &__logo-img{
        width: 40px;
        height: 40px;
    }
    

    &__logo-title{
        font-family: Inter;
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        line-height: 24px;
        text-transform: uppercase;
        
        color: #000000;
        @media screen and (max-width:823px){
            display: none;
            
        }
    }
    &__logo-description{
        font-family: Inter;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 17px;
        /* identical to box height */
        @media screen and (max-width:823px){
            display: none;
            
        }
        
        
        color: #9D9D9D;
    }

    &__list-info{
        display: flex;
    }
    &__item-info:not(:first-child){
        margin-left:30px;
       
    }
   &__item-info{
        &--cart{
            display: flex;
        }
   }

    &__price{
        margin-left: 10px;
        font-family: Inter;
        font-style: normal;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        /* identical to box height */
        
        
        color: #5C5C5C;
    }
}