.shop {
  width: 100%;
  background: white;
  position: relative;
}

.shop--cart-open::after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  z-index: 10;
  background: rgba(0, 0, 0, 0.5);
}

._container {
  max-width: 1080px;
  margin: auto;
  padding: 0 15px;
}

.product-in-cart {
  width: 385px;
  height: 100%;
  position: fixed;
  z-index: 100;
  background: white;
  top: 0;
  right: 0;
  padding: 32px 0 0 30px;
}

.product-in-cart__title {
  margin-bottom: 30px;
}

@media screen and (max-width: 360px) {
  .product-in-cart__title {
    text-align: center;
  }
}

.product-in-cart__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 325px;
  border: 1px solid #F3F3F3;
  border-radius: 20px;
  padding: 30px 20px;
  margin-bottom: 20px;
}

@media screen and (max-width: 360px) {
  .product-in-cart__list {
    margin-left: 20px;
  }
}

.product-in-cart__item--block-img {
  width: 70px;
  height: 70px;
}

.product-in-cart__img {
  width: 100%;
  height: 100%;
}

.product-in-cart__description {
  width: 150px;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 8px;
  color: #000000;
}

.product-in-cart__price {
  font-family: Inter;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
  color: #000000;
}

.product-in-cart-footer {
  position: absolute;
  bottom: 20px;
}

@media screen and (max-width: 360px) {
  .product-in-cart-footer {
    right: 0px;
  }
}

.product-in-cart-footer__btn {
  width: 100%;
  margin-top: 24px;
  margin-bottom: 30px;
}

.product-in-cart-empty__title {
  text-align: center;
  margin-bottom: 9px;
}

.product-in-cart-empty__title--green {
  color: #87C20A;
}

.product-in-cart-empty-container {
  margin-top: 109px;
  margin-left: 20px;
}

.product-in-cart-empty__img {
  display: block;
  margin: auto;
  margin-bottom: 20px;
  width: 120px;
  height: 120px;
}

.product-in-cart-empty__text {
  width: 285px;
  text-align: center;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  /* or 150% */
  text-align: center;
  color: rgba(0, 0, 0, 0.4);
  margin: auto;
  margin-bottom: 40px;
}

.product-in-cart-empty__btn {
  margin: auto;
}

.button {
  border: none;
  display: block;
  background: #9DD458;
  color: white;
  padding: 17px 0 19px 0;
  width: 245px;
  border-radius: 18px;
  cursor: pointer;
}

.button__text {
  font-family: Inter;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #FFFFFF;
  display: inline-block;
  margin-left: 18px;
}

.button__text--order {
  margin-right: 18px !important;
  margin-left: 0 !important;
}
